import React from "react";
import {
  Form,
  Select,
  Input,
  Button,
  Row,
  Col,
  Divider,
  Modal,
  Pagination,
  Image,
  Carousel,
} from "antd";
import axios from "axios";
import { Link } from "react-router-dom";
import styled from "styled-components";
import { ToastContainer, toast } from "react-toastify";
import { FormInstance } from "antd/lib/form";
import "react-toastify/dist/ReactToastify.css";
import { CloudUploadOutlined } from "@ant-design/icons";
import QuestionList from "./QuestionList";

const Option = Select.Option;
const category = ["","","","","","","","",""];
const { TextArea } = Input;

const Question = (props) => {
  const pageSize = parseInt(window.innerHeight / 70);
  //      (height    )
  const [dataSize,setdataSize] = React.useState(0);
  const [form] = Form.useForm();
  const [visible, setVisible] = React.useState(false);
  const [data, setData] = React.useState([]);
  const [page, setPage] = React.useState(0);
  const [dep,setDep] = React.useState([]);
  const token = localStorage.getItem("user_token");
  const header = {
    headers: {
      authorization: `${token}`,
    },
  };

  const loadDep = () => {
    axios
      .get("https://sjswbot.site/dep")
      .then((res) => {
        setDep(res.data.result);
      })
      .catch((error) => {
        console.log("")
      });
  }

  const FormHandler = () => {
    setVisible(true);
  };

  const handleOk = (e) => {
    setVisible(false);
  };

  const handleCancel = (e) => {
    setVisible(false);
  };

  const PageRefresh = () => {
    window.scrollTo({ top: 0, behavior: "smooth" });
    // data     

    return data.map((it, i) => {

      return (
        <QuestionList
          key={i}
          data={it}
          count={dataSize - i - pageSize * (page)}
          getData={getData}
          setPage={setPage}
          pageSize={pageSize}
          page={page}
        />
      );
    });
  };

  const onFinishFunc = async (formData) => {
    //  
    for (const [key, value] of Object.entries(formData)) {
      if (value === undefined || value === null || value === NaN) {
        formData[key] = "";
      }
    }

    const response = await axios
      .post(`https://sjswbot.site/knowledgePlus`, formData, header, { widthCredentials:true })
      .then((res) => {
        console.log(res);
        if (res.status === 200) {
          toast.success(" !");
          setPage(0);
          getData();
          setVisible(false);
          form.setFieldsValue({
            faqno: "",
            category1: "",
            category2: "",
            category3: "",
            category4: "",
            question: "",
            questionAnswer: "",
            landingUrl: "",
            imageinfo: "",
          });
        }
      })
      .catch((error) => {
        toast.error("  !");
      });
  };

  const onValuesChange = (changedValue, allValue) => {
    console.log(changedValue);
  };

  const onPageChange = (pagenum) => {
    //pagenum 1,2,3,4   .
    setPage(pagenum-1);
  };

  const getData = React.useCallback(async () => {
    const response = await axios.get(`https://sjswbot.site/knowledgePlus?page=${page}&size=${pageSize}`,header,{ widthCredentials: true });
    console.log(response);
    setdataSize(response.data.result.count);
    setData(response.data.result.rows);
  }, [page,setPage]);

  React.useEffect(() => {
    getData();
    loadDep();
  }, [page,setPage]);

  return (
    <div
      style={{
        margin: "3% 10%",
        padding: "1% 0%",
        display: "flex",
        alignItems: "center",
        flexDirection: "column",
        background: "white",
        borderRadius: "0.5rem",
        border: "2px solid lightgray",
      }}
    >
      <div
        style={{
          textAlign: "center",
          fontSize: "30px",
          fontFamily: "Gothic A1",
        }}
      >
        <p>+  / </p>
      </div>
            .
      <div style={{ display: "flex", flexDirection: "row", margin: "10px 0 2rem" }}>
        <Link to="/userquestion">
          <u> </u>
        </Link>{" "}
           
      </div>
      {PageRefresh()}
      <div style={{ marginBottom: "2rem" }} />
      <Pagination
        current={page+1}
        total={dataSize}
        defaultPageSize={pageSize}
        onChange={onPageChange}
        style={{ marginBottom: "1.5rem" }}
      />
      <Button onClick={FormHandler}> </Button>
      <Modal
        title=""
        visible={visible}
        onOk={handleOk}
        onCancel={handleCancel}
        maskClosable={false}
        footer={[null, null]} //ok cancel   
        width="40rem"
      >
        <Form
          form={form}
          onFinish={onFinishFunc}
          onFieldsChange={onValuesChange}
          autoComplete="off"
          style={{ width: "95%", padding: "0 5%" }}
        >
          <Form.Item
            label=""
            name="question"
            rules={[
              {
                required: true,
                message: " ",
              },
            ]}
            required
          >
            <Input />
          </Form.Item>
          <Form.Item
            label=""
            name="questionAnswer"
            rules={[
              {
                required: true,
                message: " ",
              },
            ]}
            required
          >
            <Input.TextArea 
                  initialvalues={data.questionAnswer} 
                  style={{height:"10rem", resize:"none"}}
            />  
          </Form.Item>
          <Form.Item
            label=""
            name="category1"
            rules={[
              {
                required: true,
                message: " ",
              },
            ]}
            required
          >
            <Select>
                {dep.map(i => (i.department != "") ? <Option value={i.department}>{i.department}</Option>:null)}
            </Select>
          </Form.Item>
          <Form.Item 
            label="" 
            name="category2"
            rules={[
              {
                required: true,
                message: " ",
              },
            ]}
            required
            >
              <Select>
                  {category.map(i => <Option value={i}>{i}</Option>)}
              </Select>
          </Form.Item>
          <Form.Item label=" 1" name="category3">
            <Input />
          </Form.Item>
          <Form.Item label=" 2" name="category4">
            <Input />
          </Form.Item>
          <Form.Item label=" 3" name="category5">
            <Input />
          </Form.Item>
          <Form.Item label="" name="landingUrl">
            <Input />
          </Form.Item>
          <Form.Item label="" name="imageinfo">
            <Input />
          </Form.Item>
          <Form.Item colon={false} wrapperCol={{ offset: 11 }}>
            <Button
              icon={<CloudUploadOutlined />}
              htmlType="submit"
              style={{ margin: "1rem 1rem 0 1rem" }}
            >
              
            </Button>
          </Form.Item>
        </Form>
      </Modal>
    </div>
  );
};

export default Question;
